import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { ReanderField } from "../../../components/helpers/field";
import ValidasiMasterTukang from "../../../Validasi/ValidasiMasterTukang.jsx";

const maptostate = (state) => {
  if (state.datamaster.ShowModalTukang !== null) {
    return {
      initialValues: {
        kode_tukang: state.datamaster.ShowModalTukang.kode_sales,
        nama_tukang: state.datamaster.ShowModalTukang.nama_tukang,
      },
    };
  }
};
class FormModalTukang extends Component {
  state = {
    aktif: false,
  };

  componentDidMount() {
    setTimeout(() => {
      this.props.isEdit
        ? document.getElementById("nama_tukang").focus()
        : document.getElementById("kode_tukang").focus();
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <Field
          tabIndex="1"
          id="kode_tukang"
          name="kode_tukang"
          component={ReanderField}
          type="text"
          label="Kode Tukang"
          placeholder="Masukan Kode Tukang"
          readOnly={this.props.isEdit}
        />
        <Field
          tabIndex="2"
          id="nama_tukang"
          name="nama_tukang"
          component={ReanderField}
          type="text"
          label="Nama Tukang"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Nama Tukang"
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="3"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormModalTukang = reduxForm({
  form: "ModalTukang",
  enableReinitialize: true,
  validate: ValidasiMasterTukang,
})(FormModalTukang);
export default connect(maptostate, null)(FormModalTukang);
